import orderStatus from './orderStatus.vue';
import WeVue from 'we-vue';

export default {
  data() {
    return {
      isActive: null,
      allOrder: [],
      orderStatus: []
    };
  },
  beforeRouteLeave (to, from, next) {
    this.$store.commit('isMenu', true);
    next();
  },
  created() {
    WeVue.Indicator.open({
      text: 'loading',
      spinnerType: 'double-snake'
    });
    this.axios({
        method: 'post',
        url: '/api/cashier/Cashier/GetCashierOrderListByDate',
        data: {
          ShopId: 34,
          Date: '2017-09-06',
          DeviceId: '1F2DAA7B-4EA9-429A-B2DD-D48B1C762ACE'
          }
      }).then((res) => {
        WeVue.Indicator.close();
        res = res.data.Data;
        this.allOrder = [];
        res.forEach((val) => {
          this.orderDetail(val.Number);
        });
      }).catch(() => {
        WeVue.Indicator.close();
        this.tipToast('网络超时，请稍后再试！');
      });

    this.$nextTick(() => {
      this.$store.commit('isLoading', false);
      this.$store.commit('isMenu', false);
    });
  },
  mounted() {
  },
  activated() {
  },
  methods: {
    tipToast (msg) {
      WeVue.Toast({
        duration: 2000,
        message: msg,
        type: 'text'
      });
    },
    orderDetail(Num) {
      WeVue.Indicator.open({
        text: 'loading',
        spinnerType: 'double-snake'
      });
      this.axios({
        method: 'get',
        url: '/api/cashier/Cashier/GetCashierOrderDetailByNumber?number=' + Num
      }).then((res) => {
        WeVue.Indicator.close();
        res = res.data.Data;
        this.allOrder.push(res);
        this.allOrder.forEach((val) => {
          let money = 0;
              val.Items.forEach((data) => {
                money += data.Quantity * data.UnitPrice;
              });
              this.$set(val, 'TotalMoney', money);
        });
        this.statusTab(this.$route.query.status, this.$route.query.index);
      }).catch(() => {
        WeVue.Indicator.close();
        this.tipToast('网络超时，请稍后再试！');
      });
    },
    statusTab(status, index) {
      this.isActive = index;
      if (this.allOrder.length) {
        this.orderStatus = [];
        if (status === 'whole') {
          this.orderStatus = this.allOrder;
        } else {
          this.allOrder.forEach((val) => {
            if (val.Base.OrderStatus === status) {
                this.orderStatus.push(val);
            }
          });
        }
      }
    }
  },
  components: {
    orderStatus
  }
};



