/* eslint-disable no-new */
import Swiper from 'swiper';
import 'swiper/dist/css/swiper.min.css';
import {mapState} from 'vuex';
import WeVue from 'we-vue';

export default {
  data() {
    return {
      switchValue: true
    };
  },
  computed: mapState({
    ParcelInfo: (data) => {
      let ParcelInfo = [];
      data.CartInfo.forEach((val) => {
        if (val.isChecked) {
          ParcelInfo.push(val);
        }
      });
      return ParcelInfo;
    },
    ParcelNum: (data) => {
      let ParcelNum = 0;
      data.CartInfo.forEach((val) => {
        if (val.isChecked) {
          ParcelNum += val.count;
        }
      });
      return ParcelNum;
    },
    TotalMoney: (data) => {
      let TotalMoney = 0;
      data.CartInfo.forEach((val) => {
        if (val.isChecked) {
          TotalMoney += val.count * (val.price || val.Price);
        }
      });
      return TotalMoney.toFixed(2);
    }
  }),
  mounted() {
  },
  activated() {
    this.$nextTick(() => {
      this.$store.commit('isLoading', false);
      new Swiper('.swiper-container-order', {
        slidesPerView: 3,
        spaceBetween: 10
      });
    });
  },
  methods: {
    tipToast (msg) {
      WeVue.Toast({
        duration: 2000,
        message: msg,
        type: 'text'
      });
    },
    settlement(ParcelInfo) {
      WeVue.Indicator.open({
        text: 'loading',
        spinnerType: 'double-snake'
      });
      let data = {
        DeviceId: '1F2DAA7B-4EA9-429A-B2DD-D48B1C762ACE',
        Items: ParcelInfo,
        ShopId: 34
      };
      this.axios({
        method: 'post',
        url: '/api/cashier/Cashier/CreateCashierOrder',
        data: data
      }).then((res) => {
        res = res.data.Data;
        WeVue.Indicator.close();
        this.$store.commit('saveOrderInfo', res);
        this.$store.commit('addCart', 'subOrder');
        localStorage.removeItem('goodsInfo');
        localStorage.removeItem('CartInfo');
        this.$router.replace('./placeOrder');
      }).catch(() => {
        WeVue.Indicator.close();
        this.tipToast('网络超时，请稍后再试！');
      });
    }
  }
};



