import VueCoreImageUpload from 'vue-core-image-upload';
import WeVue from 'we-vue';
export default {
  data() {
    return {
      valueContent: this.valueContent,
      src: []
    };
  },
  activated () {
    this.$nextTick(() => {
      this.$store.commit('isLoading', false);
    });
  },
  components: {
    'vue-core-image-upload': VueCoreImageUpload
  },
  methods: {
    imageuploaded(res) {
      if (res.Result === 1) {
        if (this.src.length < 5) {
          this.src.push('http://t1.itsmore.com:62006/files/images/news/image/' + res.Data[0]);
        } else {
          WeVue.Toast({
            duration: 2000,
            message: '最多只能上传5张图片！',
            type: 'text'
          });
        }
      }
    },
    delImg(index) {
      WeVue.Dialog.confirm(WeVue.Dialog.setDefaults({
        message: '确定要删除这张图片吗？',
        showCancelBtn: true,
        confirmText: '确认',
        cancelText: '取消'
        })).then(action => {
        this.src.splice(index, 1);
      });
    }
  }
};



