import Bscroll from 'better-scroll';
import WeVue from 'we-vue';

export default {
  props: ['goodsInfo', 'isMove'],
  data() {
    return {
      GoodsPackage: []
    };
  },
  methods: {
    tipToast (msg) {
      WeVue.Toast({
        duration: 2000,
        message: msg,
        type: 'text'
      });
    },
    closeLayer() {
      this.$emit('isClose', false);
    },
    DetermineSpecifications(item, GoodsPackage) {
      if (GoodsPackage.length) {
        item.SubGoods = [];
        GoodsPackage.forEach((val) => {
          val.SubGoods.forEach((value) => {
            if (value.isActive) {
              item.SubGoods.push(value);
            }
          });
        });
      }
      this.$emit('isClose', false);
    },
    isActives(item, item2) {
      this.$nextTick(() => {
        item.SubGoods.forEach((val) => {
          this.$set(val, 'isActive', false);
        });
        this.$set(item2, 'isActive', true);
      });
    },
    loadPackage() {
      if (this.goodsInfo.IsPackage) {
        WeVue.Indicator.open({
          text: 'loading',
          spinnerType: 'double-snake'
        });
        this.axios.get('http://at.itsmore.com/cashier/Cashier/GetCashierGoodsById?goodsId=' + this.goodsInfo.GoodsId).then((res) => {
          res = res.data.Data;
          WeVue.Indicator.close();
          this.GoodsPackage = res.GoodsPackageGroups;
          this.GoodsPackage.forEach((val, index) => {
            if (!val.SubGoods.length) {
              this.GoodsPackage.splice(index, 1);
            }
          });
          this.$nextTick(() => {
            this.scroll = new Bscroll(this.$refs.GoodsSpecifications, {
              click: true
            });
          });
        }).catch(() => {
          WeVue.Indicator.close();
          this.tipToast('网络超时，请稍后再试！');
        });
      }
    }
  },
  created() {
    setTimeout(() => {
      this.loadPackage();
    }, 100);
  },
  activated() {
    setTimeout(() => {
      this.loadPackage();
    }, 100);
  }
};



