/* eslint-disable no-new */
  import Swiper from 'swiper';
  import 'swiper/dist/css/swiper.min.css';
  export default {
    props: {
      OrderDetail: {
        type: Array
      }
    },
    filters: {
      OrderStatus: (val) => {
        let status = {
          '0': '已取消',
          '1': '已完成',
          '2': '新订单',
          '3': '正在配送',
          '4': '已审核'
        };
        return status[val];
      },
      formatMoney: (val) => {
        if (Number(val)) {
          return '¥ ' + Number(val).toFixed(2);
        } else {
          return val;
        }
      }
    },
    data() {
      return {
      };
    },
    computed: {},
    mounted() {
      new Swiper('.swiper-container-order6', {
        slidesPerView: 5,
        spaceBetween: 10
      });
    }
  };



