
import XImg from 'vux/src/components/x-img/index.vue'

export default {
props: {},
data() {
return {
converImage: '',
goodsClass: []
};
},
beforeRouteEnter (to, from, next) {
next(vm => {
vm.$store.commit('isLoading', true);
});
},
created() {
this.axios.get('static/data.json').then((res) => {
res = res.data;
this.goodsClass = res.goodsClass;
this.converImage = this.goodsClass[0].converImage;
this.$store.commit('isLoading', false);
}).catch(function (err) {
this.$store.commit('isLoading', false);
console.log(err);
});
},
activated() {
this.$nextTick(() => {
this.$store.commit('isLoading', false);
});
},
computed: {
goodsClassFilter: function () {
return this.goodsClass.splice(2, this.goodsClass.length);
}
},
components: {
XImg
},
mounted() {}
};



