  import {mapState} from 'vuex';
  export default {
    data() {
      return {
        isActive: false
      };
    },
    mounted() {
      let payWay = document.getElementsByClassName('pay-way')[0];
      let oli = payWay.getElementsByTagName('li');
      for (let i = 0; i < oli.length; i++) {
        oli[i].onclick = function () {
          for (let j = 0; j < oli.length; j++) {
            oli[j].getElementsByClassName('checks')[0].innerHTML = '';
          }
          this.getElementsByClassName('checks')[0].innerHTML = '<i class="iconfont">&#xe62b;</i>';
        };
      }
    },
    activated() {
      this.$nextTick(() => {
        this.$store.commit('isLoading', false);
      });
    },
    computed: {
      ...mapState({
        goodsSubtotal: (data) => {
          let TotalMoney = 0;
          data.CartInfo.forEach((val) => {
            if (val.isChecked) {
              TotalMoney += val.count * (val.price || val.Price);
            }
          });
          return '¥ ' + TotalMoney.toFixed(2);
        }
      })
    },
    methods: {
      SelectivePay(way) {
//        console.log(way);
      }
    }
  };



