import {mapState} from 'vuex';
import WeVue from 'we-vue';
export default {
  data() {
    return {};
  },
  filters: {
    PayAmount: (val) => {
      let pay = {
        '0': '待付款',
        '2': '现金',
        '3': '银联支付',
        '4': '支付宝',
        '5': '微信支付'
      };
      return pay[val];
    },
    formatMoney: (val) => {
      if (Number(val)) {
        return '¥ ' + Number(val).toFixed(2);
      } else {
        return val;
      }
    }
  },
  activated() {
    this.$nextTick(() => {
      this.$store.commit('isLoading', false);
    });
  },
  computed: {
    ...mapState({
      'OrderGoodsInfos': state => state.CartInfo
    }),
    ...mapState({
      createOrderInfo: (data) => {
        if (!data.createOrderInfo.OrderId && localStorage.getItem('createOrderInfo')) {
          data.createOrderInfo = JSON.parse(localStorage.getItem('createOrderInfo'));
        }
        return data.createOrderInfo;
      },
      goodsSubtotal: (data) => {
        let TotalMoney = 0;
        data.CartInfo.forEach((val) => {
          if (val.isChecked) {
            TotalMoney += val.count * (val.price || val.Price);
          }
        });
        return '¥ ' + TotalMoney.toFixed(2);
      }
    })
  },
  methods: {
    tipToast (msg) {
      WeVue.Toast({
        duration: 2000,
        message: msg,
        type: 'text'
      });
    },
    cancelOrder(id) {
      WeVue.Indicator.open({
        text: 'loading',
        spinnerType: 'double-snake'
      });
      this.axios({
        method: 'post',
        url: '/api/cashier/Cashier/CancelCashierOrder',
        data: {OrderId: id}
      }).then((res) => {
        res = res.data.Data;
        WeVue.Indicator.close();
      }).catch(() => {
        WeVue.Indicator.close();
        this.tipToast('网络超时，请稍后再试！');
      });
    },
    ContinuePay(item) {
      WeVue.Indicator.open({
        text: 'loading',
        spinnerType: 'double-snake'
      });
      this.axios({
        method: 'post',
        url: '/api/cashier/Cashier/CreateCashierOrderPayments',
        data: {OrderId: item.OrderId, PayType: item.PayAmount}
      }).then((res) => {
        WeVue.Indicator.close();
        res = res.data.Data;
        this.$router.push('./payment');
      }).catch(() => {
        WeVue.Indicator.close();
        this.tipToast('网络超时，请稍后再试！');
      });
    },
    goBack() {
      this.$router.back(-1);
    }
  },
  created() {
  },
  mounted() {
  }
};



